import React,{useContext} from 'react';

import { ThemeContext } from '../../contexts/ThemeContext';

import './Responsibility.css';

import { responsibilityData } from '../../data/responsibilityData'
import ResponsibilityCard from './ResponsibilityCard';

function Responsibility() {

    const { theme } = useContext(ThemeContext);
    return (
        <div className="responsibility" id="responsibility" style={{backgroundColor: theme.secondary}}> 
             <div className="responsibility-body">
                 <div className="responsibility-description">
                    <h1 style={{color:theme.primary}}>Positions of Responsibility</h1>
                    {responsibilityData.map(resp =>(
                        <ResponsibilityCard 
                            key={resp.id}
                            id={resp.id}
                            jobtitle={resp.jobtitle}
                            company={resp.company}
                            startYear={resp.startYear}
                            endYear={resp.endYear}
                            details={resp.details}/>
                    ))}
                 </div>
            {/* <div className="responsibility-image">
                <img src={theme.respimg} alt="" />
            </div> */}
             </div>
        </div>
    )
}

export default Responsibility
