import React, { useContext} from 'react';

import './Volunteer.css';
import { ThemeContext } from '../../contexts/ThemeContext';
import { volunteerData } from '../../data/volunteerData'
import VolunteerCard from './VolunteerCard';

function Volunteer() {

    const { theme } = useContext(ThemeContext);
    return (
        <>
            {volunteerData.volunteer.length > 0 && (
                <div className="volunteer" id="volunteer" style={{backgroundColor: theme.secondary}}>
                <div className="volunteer-body">
                    <h1 style={{color: theme.primary}}>Volunteering</h1>
                    <h4 style={{color:theme.tertiary}}>{volunteerData.bio}</h4>
                </div>
                <div className="volunteer-cards">
                    {volunteerData.volunteer.map(voluntee => ( 
                        <VolunteerCard 
                        key={voluntee.id}
                        id={voluntee.id}
                        title={voluntee.title}
                        details={voluntee.details}
                        date={voluntee.date}
                        field={voluntee.field}
                        image={voluntee.image}/>
                    ))}
                </div>
            </div>
            )}
        </>
    )
}

export default Volunteer
